import React from "react";
import {
  setActiveComponent,
  changeSelectedDataset,
} from "../actions";
import { connect } from "react-redux";
import { selectCandidates, selectCatonlyCandidates, getRanges, getSkyline } from "../utils";
import imgFavorite from '../imgs/favorite.png';
import MainTitle from '../imgs/MainTitleAll.png';
import SelectCatAttr from '../imgs/SelectCatAttr.png'
import '../css/welcome.css';
import {config} from "@fortawesome/fontawesome-svg-core";

// the welcome scene containing a brief introduction and a table to obtain the user's input
class Welcome extends React.Component {
  state = {
    selectedDataset: this.props.selectedDataset
  }

  handleStart = () => {
    if(['car'].includes(this.props.selectedDataset))
    {
      this.props.startCar();
    }
    else
    {
      this.props.startNBA();
    }
  };

  handleChange = event => {
    this.setState({ selectedDataset: event.target.value });
    this.props.changeSelectedDataset(event.target.value);
  };

  render() {
    const datasetChoose = [];
      datasetChoose.push(
      <tr>
      <td className="align-middle" colSpan="2">
        <div className="form-check form-check-inline custom-radio-spacing">
          <input
              className="form-check-input"
              type="radio"
              name="dataset"
              value="car"
              onChange={this.handleChange}
              id="carRadio"
              checked={this.state.selectedDataset === 'car'}
          />
          <label className="form-check-label" htmlFor="carRadio">
            Car Dataset
          </label>
        </div>
        <div className="form-check form-check-inline">
          <input
              className="form-check-input"
              type="radio"
              name="dataset"
              value="nba"
              onChange={this.handleChange}
              id="nbaRadio"
              checked={this.state.selectedDataset === 'nba'}
          />
          <label className="form-check-label" htmlFor="nbaRadio">
            NBA Dataset
          </label>
        </div>
      </td>
    </tr>
    );


    return (
        <div className="text-center m-auto" style={{ maxWidth: "100%" }}>
          <img alt='' src={MainTitle} style={{ 'width': '100%' }} />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          {/* Dataset */}
          <h4 style={{
            'background': 'linear-gradient(to right, #89bfe9, #5271a6)', // 
            'borderRadius': '5px',
            'textAlign': 'left',
            'padding': '5px',
            'boxShadow': '3px 3px 5px rgba(0, 0, 0, 0.5)'  // 
          }}>
            Please select the Dataset for Recommendation
          </h4>
          <table className="table">
            <tbody>{datasetChoose}</tbody>
          </table>
          <br />
          <br />
          <br />
          <br />
          <div>
            <button
                type="button"
                className="modern-btn"
                style={{ width: "8rem" }}
                onClick={this.handleStart}
            >
              Next
            </button>
          </div>
          <br/>
        </div>
    );
  }
}

const mapStateToProps = ({selectedDataset}) => ({
    selectedDataset
});

const mapDispatchToProps = dispatch => ({
  startCar: () => {
    dispatch(setActiveComponent("CarWelcome"));
  },
  startNBA: () => {
    dispatch(setActiveComponent("NBAWelcome"));
  },
  changeSelectedDataset: ds => dispatch(changeSelectedDataset(ds))
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Welcome);